@mixin font-size-default($size: 14) {
    $lineHeight: $size + 4;
    font-size: $size + px;
    line-height: $lineHeight + px;
}

@mixin mobile-block() {
    @media screen and (max-width: 768px) {
        width: 100%;
    }
}

@mixin multi-line ($num) {
    word-break: break-all;
    display: -webkit-box;
    -webkit-line-clamp: $num;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

@mixin single-line {
    overflow: hidden;
    text-overflow: ellipsis;
    -o-text-overflow: ellipsis;
    -webkit-text-overflow: ellipsis;
    -moz-text-overflow: ellipsis;
    white-space: nowrap;
}
.operator {
  padding: 24px;
  background-color: var(--block-color);
  border-radius: var(--block-radius);

  &-header {
    display: flex;
    flex-direction: row;
    &-name-wrapper {
      flex: 1;
      margin-left: 12px;
      overflow: hidden;
    }
  }

  &-name {
    margin-bottom: 4px;
  }
  &-description {
    color: var(--text-second-color);
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}

.github {
  display: flex;
  align-items: center;
  & > a {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
