@mixin font-size-default($size: 14) {
    $lineHeight: $size + 4;
    font-size: $size + px;
    line-height: $lineHeight + px;
}

@mixin mobile-block() {
    @media screen and (max-width: 768px) {
        width: 100%;
    }
}

@mixin multi-line ($num) {
    word-break: break-all;
    display: -webkit-box;
    -webkit-line-clamp: $num;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

@mixin single-line {
    overflow: hidden;
    text-overflow: ellipsis;
    -o-text-overflow: ellipsis;
    -webkit-text-overflow: ellipsis;
    -moz-text-overflow: ellipsis;
    white-space: nowrap;
}
$linkColor: #3a99d8;

.detail_container {
  flex: 1;
  // width: var(--detail_width);
  width: 85rem;
  margin: 20px auto;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  @include mobile-block;
}

.post_container {
  flex: 1;
  display: flex;
  justify-content: center;
  overflow: auto;
}

.post {
  position: relative;
  width: 100%;
  margin-bottom: 32px;

  &_content {
    background: #ffffff;
    padding: 36px;
    border-radius: var(--block-radius);


    @media screen and (max-width: 768px) {
      padding: 16px;
    }
  }

  &_header {
    // border-bottom: 1px solid #e7e7e7;
    // padding: 20px 0;
    margin-bottom: 1.8rem;

    .name {
      display: block;
      font-weight: 700;
      margin-bottom: 12px;
      @include font-size-default(36);

      @media screen and (max-width: 768px) {
        font-size: 2rem;
      }
    }

    .base-info {
      display: flex;
      justify-content: space-between;
      color: var(--text-third-color);
      font-weight: 300;

      .item {
        font-size: 12px;
      }
    }
  }

  &_headerImg {
    max-width: 100%;
    min-width: 100%;
    max-height: 400px;
    object-fit: cover;
    object-position: center;
    margin-bottom: 2.5rem;
    border-radius: 4px;
  }

  &_body {
    padding: 30px 20px;

    .desc {
      border-left: 8px solid #dddfe4;
      background: #eef0f4;
      margin: 0 0 24px;
      padding: 16px;
      overflow: auto;
    }
  }

  .operations {
    position: fixed;
    top: 200px;
    left: calc((100vw - 1000px) / 2 - 48px);

    &_list {
      background-color: #ffffff;
    }

    &_item {
      display: block;
      padding: 12px;
      height: 1;
      cursor: pointer;

      >svg {
        width: 20px;
        height: 20px;
      }

      >span {
        display: block;
        text-align: center;
        line-height: 1;
      }
    }

    &_item_active {
      color: #ff7474;
      border-color: #ff7474;
    }
  }

  &_notice {
    margin-top: 48px;
    padding: 16px;
    background-color: var(--body-bg-color);
    @include font-size-default(14);
    color: var(--text-second-color);
    border-radius: var(--radius);

    > div:not(:last-child) {
      margin-bottom: 4px;
    }
  }
}

.main {
  flex: 1;
  display: flex;
  flex-direction: row;

  @media screen and (max-width: 768px) {
    flex-direction: column;
  }

  .content {
    flex: 1;
    overflow: hidden;
  }

  .sidebar {
    width: 18rem;
    margin-left: 20px;

    // @include mobile-block;
    @media screen and (max-width: 768px) {
      // max-width: unset;
      // margin-left: unset;
      display: none;
    }

  }
}



.ads {
  margin-bottom: 24px;
}

.comments {
  @media screen and (max-width: 768px) {
    padding: 0 16px;
  }
}